/*  */
import Vue from 'vue'
import Vuex from 'vuex'
 
Vue.use(Vuex);
//
const state = { 
  // --- data
  num: 666,  //
  status: true, //   falsetrue
}
// mutation 
const mutations = {
  // --- methods
  //state 
  showorclosefn (state,type) {// store.commit('showorclosefn', 10)
    console.log(state)
    state.status = !state.status;
  },
  closefn (status) {
    state.status = false;
  },
}
// actions 
const actions = {
  showorclosefn (context) {
    context.commit('showorclosefn')
  }
}
 
export default new Vuex.Store({
  state,
  mutations
})



