<template>
    <div>
           <div style="position: absolute;top:0;left:0;bottom:0px;right:0px; background-color:#333;opacity: 0.5;z-index: 1;"></div>
        <div style="position: absolute;width: 100vw;z-index: 2;">
        <el-container>
           
            <el-main style="margin-top:2rem;">
                <el-row >
                    <el-col style="background-color:#fff;" :span="12" :offset="6">
                        <div >
                            <div class="changstyle1" >
                                  <div style="width:18px;height:18px;position:absolute;top:0rem;right:0.5rem;z-index:111">
                                          <el-button style="border:0px solid #fff;padding:0rem;" @click="cancel3">
                                                <img :src="cancel" alt="" width='25px'>
                                          </el-button>
                                </div>
                                <el-row>
                                    <el-col :span="24"><div class="changstyle2"></div></el-col>
                                </el-row>
                               <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="6rem" >
                                   
                                    <el-form-item style="line-height:-1rem;" label="" prop="username">
                                        <el-input placeholder="" v-model="ruleForm2.username"></el-input>
                                    </el-form-item>
                                    <el-form-item style="line-height: 1rem!important;" label="" prop="password">
                                        <el-input type="password" placeholder="" v-model="ruleForm2.password" autocomplete="off"></el-input>
                                    </el-form-item>
                                    <el-form-item label="" prop="checkPass">
                                        <el-input type="password" placeholder="" v-model="ruleForm2.checkPass" autocomplete="off"></el-input>
                                    </el-form-item>
                                    <el-form-item label="" prop="telphone">
                                        <el-input  type="tel" placeholder="" v-model.number="ruleForm2.telphone"></el-input>
                                    </el-form-item>
                                   <el-form-item label="" prop="remark">
                                        <el-input placeholder="" v-model="ruleForm2.remark"></el-input>
                                    </el-form-item>
                                    <el-form-item label="" prop="level">
                                        <el-input placeholder="1,2,3,4" v-model="ruleForm2.level"></el-input>
                                    </el-form-item>
                                     <el-form-item label="" prop="affiliation" >
                                        <el-input placeholder=":  X X " v-model="ruleForm2.affiliation"></el-input>
                                    </el-form-item>
                                     <el-form-item label="" prop="login" >
                                        <el-radio v-model="ruleForm2.login" label=""></el-radio>
                                        <el-radio v-model="ruleForm2.login" label=""></el-radio>
                                    </el-form-item>
                                        <el-button type="primary" @click="submitForm('ruleForm2')"></el-button>
                                </el-form>
                            </div>
                        </div>
                    </el-col>
                </el-row>
            </el-main>
        </el-container>
        </div>
    </div>
</template>
<script>
import ajax from '../common/ajax';
import cancel from '../../static/cancel.png';
export default {
  
    name:'register',
    data(){
         var checknumber = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        }
        setTimeout(() => {
            if (!Number.isInteger(value)) {
                callback(new Error(''));
            }else if(value < 10000000000){
                    callback(new Error('11'));
            }else  if(value > 100000000000){
                callback(new Error('11'));
            }else{
                callback();
            }
            }, 1000);
        };
        var checkLevel = (rule, value, callback) => {
            if (!value) {
            return callback(new Error(''));
            }else if(value == '' || value == '' || value == '' || value == '' ) {
                callback();
            }else if (value != '' || value != '' || value != '' || value != '' ) {
                callback(new Error(''));
            }
        };
         var validatePass = (rule, value, callback) => {
            if (value === '') {
            callback(new Error(''));
            } else {
            if (this.ruleForm2.checkPass !== '') {
                this.$refs.ruleForm2.validateField('checkPass');
            }
            callback();
            }
        };

        var validateaffiliation = (rule, value, callback) => {
            if (value === '') {
            callback(new Error(''));
            } else {
            callback();
            }
        };
         var validateremark = (rule, value, callback) => {
            if (value === '') {
            callback(new Error(''));
            } else {
            callback();
            }
        };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.ruleForm2.password) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
         return{
             cancel,
            ruleForm2: {
                       uid:window.localStorage.getItem('uid'),
                id:'',
                password: '',
                checkPass: '',
                telphone: '',
                remark: '',
                username: '',
                level:'',
                affiliation:'',
                login:'',
            },
            rules2: {
                password: [
                    { validator: validatePass, trigger: 'blur' }
                ],
                checkPass: [
                    { validator: validatePass2, trigger: 'blur' }
                ],
                username: [
                    { required: true,message: '', trigger: 'blur' },
                      { min: 3, max: 15, message: ' 3  15 ', trigger: 'blur' }
                    
                ],
                remark: [
                    { validator: validateremark, trigger: 'blur' },
                    { mix:1, max:50,message: '1-50', trigger: 'blur' }
                ],
                telphone: [
                    { validator: checknumber, trigger: 'blur' },
                  
                ],
                level: [
                    { validator: checkLevel, trigger: 'blur' },
                ],
                affiliation: [
                    { validator: validateaffiliation, trigger: 'blur' },
                    { mix:1, max:50,message: '', trigger: 'blur' }
                ],
            }
        }
    },
     methods: {
         cancel3(){
                this.$emit('closeparent3');
         },
      submitForm(formName) {
           this.$confirm('', '', {
                distinguishCancelAndClose: true,
                confirmButtonText: '',
                cancelButtonText: ''
            })
            .then(() => {
                this.$message({
                type: 'info',
                message: '...',
                });
             this.$refs[formName].validate((valid) => {
                if (valid) {
                    var url = "http://jenk.gd.cn:8080/FB/user/add";
                    var _this = this;
                    let Base64 = require('js-base64').Base64;
                    _this.ruleForm2.password = Base64.encode(_this.ruleForm2.password);
                    _this.ruleForm2.checkPass = Base64.encode(_this.ruleForm2.checkPass);
                    var obj = {};
                    obj.password = _this.ruleForm2.password;
                    obj.telphone = _this.ruleForm2.telphone;
                      obj.uid = _this.ruleForm2.uid;
                    obj.remark = _this.ruleForm2.remark;
                    obj.username = _this.ruleForm2.username;
                    obj.affiliation = _this.ruleForm2.affiliation; 
                    obj.login = _this.ruleForm2.login;  
                    if( _this.ruleForm2.level == '') {
                        obj.level = 1 
                    }
                    else if(  _this.ruleForm2.level == '') {
                        obj.level = 2 
                    }
                    else if(  _this.ruleForm2.level == '') {
                        obj.level = 3 
                    }
                    else if(  _this.ruleForm2.level == '') {
                        obj.level= 4 
                    }
                    ajax.inAjaxpost(url,obj,(data)=>{
                        if(data.code == 108){
                            alert('');
                            return false;
                        }
                        if(data.code == 200){
                            alert('');
                        _this.ruleForm2.password = '';
                        _this.ruleForm2.telphone = '';
                        _this.ruleForm2.remark = '';
                            _this.ruleForm2.username = ''; 
                            _this.ruleForm2.level= '';
                        _this.ruleForm2.checkPass= '';
                            _this.ruleForm2.affiliation= '';
                            _this.ruleForm2.login= '';
                            this.$emit('closeparent1');
                        }
                    })
                } 
                })
            })
            .catch(action => {
                this.$message({
                type: 'info',
                message: action === 'cancel'
                    ? ''
                    : ''
                })
            });
       
      }
     }
}
</script>
<style scoped>
.changstyle1{
    position: relative;
    box-sizing: border-box;
     border: 2px solid #000;
}

.changstyle2{
    font-size: 1.6rem;
    padding: 0.8rem;
    color:rgb(10, 10, 10);
    font-weight: bolder;
}
.el-button--primary{
    font-size: 1.2rem;
    width:80%;
    margin-bottom: 1rem;
}
.el-form{
    width:88%;
    margin:0 auto;
}

</style>




