<template>
    <div>
        <div style="position: absolute;top:0;left:0;bottom:0px;right:0px; background-color:#333;opacity: 0.5;z-index: 1;"></div>
        <div style="position: absolute;width: 100vw;z-index: 2;">
        <el-container>
                           
            <el-main >
                <el-row >
                    <el-col style="background-color:#fff;margin-top: 4rem;" :span="12" :offset="6">
                        <div >
                            <div class="changstyle1">
                                 <div style="width:18px;height:18px;position:absolute;top:0rem;right:0.5rem;z-index:111">
                                          <el-button style="border:0px solid #fff;padding:0rem;" @click="cancel2">
                                                <img :src="cancel" alt="" width='25px'>
                                          </el-button>
                                </div>
                                <el-row>
                                    <el-col :span="24"><div class="changstyle2"></div></el-col>
                                </el-row>
                               <el-form :model="loginUsername" status-icon :rules="rules2" ref="loginUsername" label-width="6rem" >
                                    <el-form-item  label="" prop="username">
                                        <el-input placeholder="" v-model="loginUsername.username"></el-input>
                                    </el-form-item>
                                    <el-form-item label="" prop="password">
                                        <el-input type="password" placeholder="" v-model="loginUsername.password" autocomplete="off"></el-input>
                                    </el-form-item>
                                    <el-form-item label="" prop="telphone">
                                        <el-input  type="tel" placeholder="" v-model.number=" loginUsername.telphone"></el-input>
                                    </el-form-item>
                                   <el-form-item label="" prop="remark">
                                        <el-input placeholder="" v-model="loginUsername.remark"></el-input>
                                    </el-form-item>
                                    <el-form-item label="" prop="level">
                                        <el-input placeholder="1,2,3,4" v-model="loginUsername.level"></el-input>
                                    </el-form-item>
                                     <el-form-item label="" prop="affiliation">
                                        <el-input placeholder=":  X X " v-model="loginUsername.affiliation"></el-input>
                                    </el-form-item> 
                                    <el-form-item label="" prop="login" style=" margin-bottom: 0px;">
                                            <el-radio v-model="loginUsername.login" label=""></el-radio>
                                        <el-radio v-model="loginUsername.login" label=""></el-radio>
                                    </el-form-item>
                                        <el-button type="primary" @click="submitForm('loginUsername')"></el-button>
                                </el-form>
                            </div>
                        </div>
                    </el-col>
                </el-row>
            </el-main>
        </el-container>
        </div>
    </div>
</template>
<script>
import ajax from '../common/ajax';
import cancel from '../../static/cancel.png';
export default {
      
    name:'modification2',
    props:{
            loginUsername:''
    },
    data(){
         var checknumber = (rule, value, callback) => {
            if (!value) {
                return callback(new Error(''));
            }
        setTimeout(() => {
            if (!Number.isInteger(parseInt(value) )) {
                callback(new Error(''));
            }else if(value < 10000000000){
                    callback(new Error('11'));
            }else  if(value > 100000000000){
                callback(new Error('11'));
            }else{
                callback();
            }
            }, 1000);
        };
        var checkLevel = (rule, value, callback) => {
            if (!value) {
            return callback(new Error(''));
            }else if(value == '' || value == '' || value == '' || value == '' ) {
                callback();
            }else if (value != '' || value != '' || value != '' || value != '' ) {
                callback(new Error(''));
            }
        };
         var validatePass = (rule, value, callback) => {
            if (value === '') {
            callback(new Error(''));
            } else {
            callback();
            }
        };
         var validateaffiliation = (rule, value, callback) => {
            if (value === '') {
            callback(new Error(''));
            } else {
            callback();
            }
        };
         var validateremark = (rule, value, callback) => {
            if (value === '') {
            callback(new Error(''));
            } else {
            callback();
            }
        };
         return{
                  cancel,

            rules2: {
                password: [
                    { validator: validatePass, trigger: 'blur' }
                ],
                username: [
                    { message: '', trigger: 'blur' },
                ],
                remark: [
                    { validator: validateremark, trigger: 'blur' },
                    { mix:1, max:50,message: '1-50', trigger: 'blur' }
                ],
                telphone: [
                    { validator: checknumber, trigger: 'blur' },
                ],
                level: [
                    { validator: checkLevel, trigger: 'blur' },
                ],
             
                affiliation: [
                    { validator: validateaffiliation, trigger: 'blur' },
                    { mix:1, max:50,message: '', trigger: 'blur' }
                ],
            }
        }
    },
     methods: {
         cancel2(){
             this.$emit("closeparent20")
         },
      submitForm(formName) {
           this.$confirm('', '', {
                distinguishCancelAndClose: true,
                confirmButtonText: '',
                cancelButtonText: ''
            })
            .then(() => {
                this.$message({
                type: 'info',
                message: '',
                });
                   this.$refs[formName].validate((valid) => {
                    if (valid) {
                    var url = "http://jenk.gd.cn:8080/FB/user/update";
                    let Base64 = require('js-base64').Base64;
                    this.loginUsername.password = Base64.encode(this.loginUsername.password);
                    var obj = {};
                         obj.uid = this.loginUsername.id;
                    obj.id = this.loginUsername.id;
                    obj.password = this.loginUsername.password;
                    obj.telphone =this.loginUsername.telphone ;
                    obj.remark = this.loginUsername.remark;
                    obj.username = this.loginUsername.username;
                    obj.affiliation = this.loginUsername.affiliation; 
                    obj.login = this.loginUsername.login;  
                    if( this.loginUsername.level == '') {
                        obj.level = 1 
                    }
                    else if(  this.loginUsername.level == '') {
                        obj.level = 2 
                    }
                    else if(  this.loginUsername.level == '') {
                        obj.level = 3 
                    }
                    else if(  this.loginUsername.level == '') {
                        obj.level= 4 
                    }
                    ajax.inAjaxpost(url,obj,(data)=>{
                        if(data.code == 108){
                            alert('');
                            return false;
                        }
                        if(data.code == 200){
                            window.localStorage.setItem("saveusername" , obj.username );
                             this.$emit("closeparent21")
                        }
                    })
                } 
                })
            })
            .catch(action => {
                this.$message({
                type: 'info',
                message: action === 'cancel'
                    ? ''
                    : ''
                })
            });
        
      }
     },

}
</script>
<style scoped>
.changstyle1{
    position: relative;
    box-sizing: border-box;
     border: 2px solid #000;
}
.changstyle2{
    font-size: 1.6rem;
    padding: 1.5rem;
    color:rgb(10, 10, 10);
    font-weight: bolder;
}
.el-button--primary{
    font-size: 1.2rem;
    width:80%;
    margin: 1rem 0;
}
.el-form{
    width:88%;
    margin:0 auto;
}
</style>




